@use "../global/variables.scss";

.catalog-table__rows {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 140px;
  padding-right: 3px;

  @media (min-width: variables.$screen-tablet) {
    width: 32.55vw;
    border-bottom: 1px solid #cdcdcd;
    padding: 4.8px 0;

    &:last-child {
      padding-top: 7px;
    }

    @media (min-width: variables.$screen-desktop) {
      &:last-child {
        padding-top: 4px;
        padding-bottom: 10px;
      }
    }
  }

  @media (min-width: variables.$screen-desktop) {
    width: 11.73vw;
    max-width: 169px;
    border-bottom: 1px solid #cdcdcd;
    padding: 6.5px 0;
  }
}

.catalog-table__description {
  padding: 0;
}
