@use "../global/variables.scss";

.more {
  display: flex;
  align-items: flex-end;
  justify-content: center;

  &::before {
    @media (min-width: variables.$screen-tablet) {
      content: "";
      width: 100%;
      display: block;
      position: absolute;
      z-index: -1;
      top: 81px;
      bottom: 0;
      left: 0;
      border: 2px solid #f2f2f2;
    }
  }
}

.more .catalog-item__container {
  @media (min-width: variables.$screen-tablet) {
    position: relative;
    bottom: 16px;
    display: flex;
    flex-direction: column;
    gap: 54px;
    max-height: 339px;
    justify-content: flex-end;
  }

  @media (min-width: variables.$screen-desktop) {
    gap: 91px;
  }
}

.more__container {
  margin: 0 auto;

  &::before {
    content: "";
    background-image: url("../img/sprite.svg#catalog_plus");
    background-position: center;
    background-repeat: no-repeat;
    display: block;
    margin: 0 auto;
    width: 30px;
    height: 30px;

    @media (min-width: variables.$screen-tablet) {
      width: 60px;
      height: 60px;
      margin-bottom: 50px;
    }

    @media (min-width: variables.$screen-desktop) {
      margin-bottom: 40px;
    }
  }
}

.more__title {
  font-family: variables.$font-oswald;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
  text-align: center;
  padding-top: 23px;

  @media (min-width: variables.$screen-tablet) {
    padding: 0 40px 23px 40px;
    font-size: 20px;
    line-height: 24px;
  }

  @media (min-width: variables.$screen-desktop) {
    padding: 0 15px 26px 15px;
  }
}

.more__description {
  display: block;
  font-family: variables.$font-lato;
  font-size: 12px;
  line-height: 16px;
  padding-top: 8px;
  text-align: center;
  padding-bottom: 20px;

  @media (min-width: variables.$screen-tablet) {
    font-size: 16px;
    line-height: 18px;
    padding: 0 40px 29px 40px;
  }

  @media (min-width: variables.$screen-desktop) {
    font-size: 16px;
    line-height: 18px;
    padding: 0;
  }
}

.more__button {
  display: flex;
  justify-content: center;
}
