body {
  margin: 0;
}

img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  display: block;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
.link {
  text-decoration: none;
  color: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.description {
  margin: 0;
  padding: 0;
}

.title {
  margin: 0;
  padding-left: 0;
}

.list {
  list-style-type: none;
  padding: 0;
  margin: 0 auto;
}

.input {
  margin: 0;
  font-family: "Oswald", "Tahoma", sans-serif;
  padding: 13px 15px 13px 15px;
  border: 1px solid #e7e7e7;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
  flex-grow: 1;
  max-height: 45.6px;
}
@media (min-width: 768px) {
  .input {
    font-size: 20px;
    padding: 12px;
    padding-left: 19px;
    max-height: none;
  }
}
.input:hover {
  border-color: #cdcdcd;
}
.input:focus {
  border-color: #444444;
  outline: 1px solid #444444;
}
.input:invalid {
  border-color: #ff8282;
  outline: 1px solid #ff8282;
}

.fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

.fieldset__legend--catowner {
  padding-top: 26px;
}
.fieldset__legend--catowner .legend__description {
  width: 227px;
}
@media (min-width: 768px) {
  .fieldset__legend--catowner .legend__description {
    padding-right: 39px;
    width: auto;
  }
}
@media (min-width: 768px) {
  .fieldset__legend {
    padding-top: 0;
  }
}

.label {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  gap: 13px;
  white-space: nowrap;
  cursor: pointer;
}
@media (min-width: 768px) {
  .label {
    font-size: 20px;
  }
}

.label__name {
  position: relative;
  display: flex;
  align-items: center;
  gap: 15px;
}
@media (min-width: 768px) {
  .label__name {
    gap: 19px;
  }
}
.label__name--radio::before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid #cdcdcd;
}
@media (min-width: 768px) {
  .label__name--radio::before {
    width: 40px;
    height: 40px;
  }
}
.label__name--radio::after {
  content: "";
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background-color: #68b738;
  position: absolute;
  left: 8.5px;
  display: none;
}
@media (min-width: 768px) {
  .label__name--radio::after {
    width: 18px;
    height: 18px;
    left: 11px;
  }
}
.label__name--checkbox::before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
}
@media (min-width: 768px) {
  .label__name--checkbox::before {
    width: 40px;
    height: 40px;
  }
}
.label__name--checkbox::after {
  content: "";
  background-image: url("../img/sprite.svg#checkbox");
  background-repeat: no-repeat;
  background-size: contain;
  width: 18px;
  height: 17px;
  border-radius: 50%;
  position: absolute;
  top: 7px;
  left: 7.5px;
  display: none;
}
@media (min-width: 768px) {
  .label__name--checkbox::after {
    width: 26px;
    height: 26px;
    left: 8px;
  }
}

.button {
  font-family: "Oswald", "Tahoma", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  text-transform: uppercase;
  white-space: nowrap;
  cursor: pointer;
  border: none;
  padding: 0;
  text-align: center;
}
.button--primary {
  background-color: #68b738;
}
.button--primary:hover {
  background-color: #5eaa2f;
}
.button--primary:focus {
  outline: 2px solid #fdc305;
}
.button--primary:active {
  outline: none;
}
.button--primary:active .button__description {
  opacity: 0.3;
}
.button--secondary {
  background-color: #f2f2f2;
  color: #444444;
}
.button--secondary:hover {
  background-color: #ebebeb;
}
.button--secondary:focus {
  outline: 2px solid #444444;
}
.button--secondary:active {
  outline: none;
}
.button--secondary:active .button__description {
  opacity: 0.3;
}

.button__description {
  display: block;
  padding: 12px;
  white-space: normal;
  font-size: 16px;
}
.button__description--index {
  font-size: 14px;
}
@media (min-width: 768px) {
  .button__description--form {
    font-size: 20px;
  }
}

.main-header {
  min-width: 320px;
}

.additional__label:not(:last-of-type) {
  padding-bottom: 12.5px;
}
@media (min-width: 768px) {
