  }
}
.additional__label:first-of-type {
  padding-top: 33px;
}
@media (min-width: 768px) {
  .additional__label:first-of-type {
    padding-top: 0;
  }
}
@media (min-width: 1440px) {
  .additional__label:nth-of-type(2) {
    padding-right: 32px;
  }
}
@media (min-width: 1440px) {
  .additional__label:nth-of-type(3) {
    padding-right: 94px;
  }
}
.additional__label:last-of-type {
  margin-bottom: 34px;
}
@media (min-width: 768px) {
  .additional__label:last-of-type {
    margin-bottom: 0;
  }
}

.additional__container {
  padding-bottom: 28px;
}
@media (min-width: 768px) {
  .additional__container {
    display: inline-grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto;
    column-gap: 125px;
    row-gap: 32px;
    padding-top: 56px;
    padding-left: 40px;
    padding-bottom: 0;
    margin-bottom: 15px;
  }
}
@media (min-width: 1440px) {
  .additional__container {
    grid-template-columns: auto auto auto auto;
    padding-left: 0;
    column-gap: 106px;
    margin-bottom: -14px;
  }
}

.additional__input[type=checkbox]:checked + .label__name::after {
  display: block;
}

.additional__label:hover .label__name::before {
  border-color: #bababa;
}

.additional__input:not(:disabled) + .label__name:active {
  opacity: 0.3;
}

.additional__input:disabled + .label__name {
  opacity: 0.15;
}

