.advantages__item {
  position: relative;
}
@media (min-width: 768px) {
  .advantages__item::before {
    counter-increment: section 1;
    content: counter(section);
    color: #f2f2f2;
    background: #ffffff;
    display: inline-block;
    text-align: right;
    font-size: 280px;
    line-height: 280px;
    position: absolute;
    z-index: -1;
    right: -17px;
